.auto {
  padding: 96px 120px;
  background: var(--svetlo-siniy);
  border-radius: 40px;
  position: relative;
  overflow: hidden;
}
.auto::after {
  content: "";
  position: absolute;
  right: 140px;
  top: 0;
  transform: translateY(-50%);
  width: 400px;
  height: 400px;
  border-radius: 100%;
  background-color: var(--belyy);
}
.auto__image {
  position: absolute;
  height: 70%;
  bottom: 0;
  right: 130px;
}
.auto__image img {
  height: 100%;
  object-fit: contain;
}
.auto__title {
  margin-bottom: 60px;
}
.auto__container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 20px;
}
.auto__content {
  grid-column: 7 span;
}
.auto__list {
  margin-bottom: 60px;
}
.auto__item {
  display: flex;
  align-items: center;
  gap: 20px;
  position: relative;
  font-weight: 300;
  font-size: 18px;
  line-height: 156%;
  color: var(--chernyy);
}
.auto__item:not(:last-child) {
  margin-bottom: 12px;
}
.auto__item::before {
  content: "";
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 12px;
  background-color: var(--belyy);
  background-image: url("./../img/check-green.svg");
  background-position: center center;
  background-size: 24px 24px;
  background-repeat: no-repeat;
}
.auto__desc {
  max-width: 620px;
  margin-bottom: 60px;
}
.auto__order {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  max-width: 540px;
}
.auto__btn {
  padding: 30px 60px;
}
.auto__price {
  font-weight: 300;
  font-size: 18px;
  line-height: 156%;
  color: var(--chernyy);
}
.auto__price span {
  font-weight: 400;
  font-size: 32px;
  line-height: 125%;
  color: var(--siniy1);
}

.process {
  padding: 140px 0;
  border-radius: 80px;
  background: var(--siniy2);
}
.process__title {
  color: var(--belyy);
  margin-bottom: 60px;
}
.process__items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.process-item__video {
  height: 300px;
  position: relative;
  border-radius: 22px;
  overflow: hidden;
  margin-bottom: 30px;
}
.process-item__video video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.process-item__cover {
  content: "";
  position: absolute;
  z-index: 1;
  inset: -10px;
  background: var(--chernyy);
  opacity: 0.75;
  filter: blur(6px);
}
.process-item__btn {
  position: absolute;
  z-index: 3;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 60px;
  height: 60px;
  border-radius: 15px;
  box-shadow: 0 14px 35px 0 rgba(11, 131, 242, 0.3);
  background: var(--siniy1);
}
.process-item__name {
  font-weight: 300;
  font-size: 22px;
  line-height: 145%;
  color: var(--belyy);
}

.steps {
  padding: 140px 0;
}
.steps__title {
  margin-bottom: 60px;
}
.steps__title span {
  color: var(--siniy1);
}
.steps__desc {
  margin: 0;
  font-weight: 300;
  font-size: 18px;
  line-height: 156%;
  color: #000;
}
.steps__items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-bottom: 60px;
}

.step {
  padding: 50px;
  border: 2px solid var(--svetlo-siniy);
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  overflow: hidden;
}
.step::after {
  content: attr(data-step);
  position: absolute;
  bottom: -12px;
  right: 50px;
  font-weight: 500;
  font-size: 100px;
  line-height: 62%;
  text-align: center;
  color: var(--zelenyy);
}
.step__title {
  margin: 0;
  margin-bottom: 40px;
  font-weight: 400;
  font-size: 32px;
  line-height: 125%;
  color: var(--chernyy);
}
.step__desc {
  margin: 0;
  margin-bottom: 40px;
  font-weight: 300;
  font-size: 18px;
  line-height: 156%;
  color: var(--chernyy);
}
.step__time {
  display: flex;
  align-items: center;
  gap: 12px;
  font-weight: 300;
  font-size: 14px;
  line-height: 171%;
  color: var(--chernyy);
}
.step__time svg {
  width: 16px;
  height: 16px;
}

.faq {
  padding: 140px 0;
}
.faq__title {
  margin-bottom: 60px;
}
.faq__items {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}
.faq__header {
  cursor: pointer;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.faq__header::after {
  content: "";
  display: block;
  width: 16px;
  height: 16px;
  transform: rotate(45deg);
  border-right: 1px solid var(--siniy1);
  border-bottom: 1px solid var(--siniy1);
  transition: transform 0.3s ease-out;
}
.faq__name {
  display: flex;
  align-items: center;
  gap: 16px;
  position: relative;
  margin: 0;
  font-weight: 500;
  font-size: 22px;
  line-height: 145%;
  color: var(--chernyy);
}
.faq__name::before {
  content: attr(data-faq);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 42px;
  height: 42px;
  background: var(--siniy1);
  border-radius: 15px;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 22px;
  line-height: 145%;
  text-align: center;
  color: var(--belyy);
}
.faq__content {
  box-sizing: content-box;
  max-height: 0;
  overflow: hidden;
  will-change: max-height;
  transition: max-height 0.3s ease-out;
}
.faq__content p {
  padding-top: 24px;
  margin: 0;
  font-family: var(--font-family);
  font-weight: 300;
  font-size: 18px;
  line-height: 156%;
  color: var(--chernyy);
}
.faq__item {
  border: 2px solid var(--svetlo-siniy);
  border-radius: 40px;
  background: var(--svetlo-siniy);
  padding: 35px 48px 35px 40px;
}
.faq__item:not(:last-child) {
  margin-bottom: 20px;
}
.faq__item.active .faq__header::after {
  transform: rotate(-135deg);
}

.extra {
  padding: 140px 0;
  background: var(--svetlo-siniy);
  border-radius: 80px;
  margin-bottom: 140px;
}
.extra__title {
  margin-bottom: 62px;
}
.extra__container {
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 340px;
}
.extra .products__item {
  grid-row: unset !important;
  grid-column: 1 span !important;
}
.extra .products__item:nth-child(1) .prod-item__image {
  width: 400px;
  height: 285px;
  top: 100px;
  left: 40px;
}
.extra .products__item:nth-child(1) .prod-item__image img {
  object-fit: contain;
}
.extra .products__item:nth-child(2) .prod-item__images {
  left: 50px;
  top: -25px;
}
.extra .products__item:nth-child(3) .prod-item__images {
  left: 50px;
}
.extra .products__item:nth-child(4) .prod-item__images {
  top: 0;
  left: 200px;
}
.extra .products__item:nth-child(4) .prod-item__image {
  width: 100%;
}
.extra .prod-item {
  background-color: var(--belyy);
  mix-blend-mode: unset;
}
@media (any-hover: hover) {
  .extra .prod-item:hover {
    background-color: var(--siniy1);
  }
}